import { NgModule, ErrorHandler, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicStorageModule } from '@ionic/storage';
import { MediaPlugin } from '@ionic-native/media';
import { File } from '@ionic-native/file';
import { NativeAudio } from '@ionic-native/native-audio';
import { Clipboard } from '@ionic-native/clipboard';
import { WheelSelector } from '@ionic-native/wheel-selector';
import { Network } from '@ionic-native/network';

// Modals
import { FilterPage } from '../pages/filter/filter';
import { RecordsList } from '../components/audio-recorder/records-list';


/**
  PROVIDERS
**/
import { Data } from '../providers/data';
import { Lyrics } from '../providers/lyrics';
import { Ideas } from '../providers/ideas';
import { IdeasData } from '../providers/ideas-data';
import { SmartAudio } from '../providers/smart-audio';
import { Utils } from '../providers/utils';
import { LocalDataProvider } from '../providers/local-data';



/**
  NON NATIVE/IONIC PLUGINS
**/
import { IonicAudioModule, AudioProvider, WebAudioProvider, defaultAudioProviderFactory } from 'ionic-audio';


@NgModule({
    declarations: [
        MyApp,
        FilterPage,
        RecordsList
      ],
    imports: [
        BrowserModule,
        HttpModule,
        IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/about/about.module.ngfactory#AboutPageModuleNgFactory', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ambient-sounds/ambient-sounds.module.ngfactory#AmbientSoundsModuleNgFactory', name: 'AmbientSounds', segment: 'ambient-sounds', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ideas-page/ideas-page.module.ngfactory#IdeasPageModuleNgFactory', name: 'IdeasPage', segment: 'ideas-page', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/lyric-editor/lyric-editor.module.ngfactory#LyricEditorModuleNgFactory', name: 'LyricEditor', segment: 'lyric-editor', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notes-tab/notes-tab.module.ngfactory#NotesTabPageModuleNgFactory', name: 'NotesTabPage', segment: 'notes-tab', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popovers/preview-options/childs/scroll-options/scroll-options.module.ngfactory#ScrollOptionsPageModuleNgFactory', name: 'ScrollOptions', segment: 'scroll-options', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popovers/preview-options/childs/share-menu/share-menu.module.ngfactory#ShareMenuPageModuleNgFactory', name: 'ShareMenuPage', segment: 'share-menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popovers/preview-options/childs/text-options/text-options.module.ngfactory#TextOptionsPageModuleNgFactory', name: 'TextOptions', segment: 'text-options', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popovers/preview-options/preview-options.module.ngfactory#PreviewOptionsPageModuleNgFactory', name: 'PreviewOptions', segment: 'preview-options', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsModuleNgFactory', name: 'Settings', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/trash/trash.module.ngfactory#TrashPageModuleNgFactory', name: 'TrashPage', segment: 'trash', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/lyrics-tab/lyrics-tab.module.ngfactory#LyricsTabPageModuleNgFactory', name: 'LyricsTabPage', segment: 'lyrics-tab', priority: 'low', defaultHistory: [] }
  ]
}),
        IonicStorageModule.forRoot(),
        IonicAudioModule.forRoot(defaultAudioProviderFactory)
    ],
    schemas: [CUSTOM_ELEMENTS_SCHEMA],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        FilterPage,
        RecordsList
    ],
    providers: [
        {
            provide: ErrorHandler,
            useClass: IonicErrorHandler
        },
        Data,
        Lyrics,
        Ideas,
        IdeasData,
        MediaPlugin,
        File,
        StatusBar,
        SplashScreen,
        SmartAudio,
        NativeAudio,
        Clipboard,
        WheelSelector,
        Network,
        Utils,
        LocalDataProvider
    ]
})
export class AppModule { }



